import React from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

const TabNFT = () => {

    const navigate = useNavigate();
    const location = useLocation();

    const goTo = (to) => {
        // 
        if (location.pathname !== to) {
            // 
            navigate(to);
        }
    };


  return (
    <div style={{ display: 'flex', justifyContent: 'space-between' }}>

      <button onClick={() => goTo('/')}></button>
      <button onClick={() => goTo('/view')}>NFT </button>
      <button onClick={() => goTo('/mint')}>NFT </button>
      <button onClick={() => goTo('/network')}></button>
      <button onClick={() => goTo('/royaltyAll')}></button>
      <button onClick={() => goTo('/mypage')}>NFT</button>
      <button onClick={() => goTo('/royalty')}></button>
      <button onClick={() => goTo('/userinfo')}></button>
    
    </div>
  );
}

export default TabNFT;
