import { useState, useEffect } from 'react';
import Papa from 'papaparse';

// NFT
export function useNFTData() {
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const csvFileUrl = '/woodnft-demo/data/nft_data.csv';

    fetch(csvFileUrl)
      .then(response => response.text())
      .then(csvText => {
        Papa.parse(csvText, {
          header: true,
          complete: (results) => {
            setData(results.data);
            setIsLoading(false);
          }
        });
      })
      .catch(error => {
        console.error("Fetching NFT data failed:", error);
        setIsLoading(false);
        // 
      });
  }, []);

  return { data, isLoading };
}


// 
export function useUserData() {
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
  
    useEffect(() => {
      const csvFileUrl = '/woodnft-demo/data/user_data.csv';
  
      fetch(csvFileUrl)
        .then(response => response.text())
        .then(csvText => {
          Papa.parse(csvText, {
            header: true,
            complete: (results) => {
              setData(results.data);
              setIsLoading(false);
            }
          });
        })
        .catch(error => {
          console.error("Fetching user data failed:", error);
          setIsLoading(false);
          // 
        });
    }, []);
  
    return { data, isLoading };
  }

// 
export function useRoyaltyDistribution() {
    const [rdData, setRdData] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
  
    useEffect(() => {
      const csvFileUrl = '/woodnft-demo/data/royaltydistribution_data.csv';
  
      fetch(csvFileUrl)
        .then(response => response.text())
        .then(csvText => {
          Papa.parse(csvText, {
            header: true,
            complete: (results) => {
              setRdData(results.data);
              setIsLoading(false);
            }
          });
        })
        .catch(error => {
          console.error("Fetching rd data failed:", error);
          setIsLoading(false);
          // 
        });
    }, []);

    // recipient
    const rdDataFlatten = [];
    rdData.forEach(rd => {
      const rdSame = {
        distributionId: rd.distributionId,
        mintNum: rd.mintNum,
        tokenId: rd.tokenId,
        profit: rd.profit,
        totalValueIncrease: rd.totalValueIncrease
      };

      if ( rd.recipient1 ) {
        const rdCopy1 = {
          ...rdSame,
          recipient: rd.recipient1,
          valueIncrease: rd.valueIncrease1,
          divident: rd.divident1,
        }
        rdDataFlatten.push(rdCopy1);
      }
      if ( rd.recipient2 ) {
        const rdCopy2 = {
          ...rdSame,
          recipient: rd.recipient2,
          valueIncrease: rd.valueIncrease2,
          divident: rd.divident2,
        }
        rdDataFlatten.push(rdCopy2);
      }
      if ( rd.recipient3 ) {
        const rdCopy3 = {
          ...rdSame,
          recipient: rd.recipient3,
          valueIncrease: rd.valueIncrease3,
          divident: rd.divident3,
        }
        rdDataFlatten.push(rdCopy3);
      }

    });

  
    return { rdData, rdDataFlatten, isLoading };
  }