import React, { useState, useEffect } from 'react';
import TabNFT from './TabNFT';
import { useRoyaltyDistribution } from "./hooks/customHooks";
import { useUser } from "./hooks/userContext";


const UserInfo = () => {

    //user,rd 
    const { user } = useUser();
    const { rdDataFlatten , isLoading } = useRoyaltyDistribution();

    //return
    if ((user === null || user === undefined)) return <div> </div>;
    if (!rdDataFlatten) return <div></div>;
    if (isLoading) return <div>Loading...</div>;

    const userRd = rdDataFlatten.filter(rd => rd.recipient === user.userHash);

    //
    let ownedRoyalty = 0;
    userRd.forEach((rd) => {
        ownedRoyalty += Number(rd.divident);
    })

    return (
        <div>
            <TabNFT />
            <h1></h1>
            <h2>{user.name}</h2>
            <h3>: {ownedRoyalty.toFixed(3)} ETH?? </h3>
            <h4>: {user.role} <br></br>: {user.occupation} <br></br>: {user.location}<br></br>: {user.processingCount}</h4>
            <p>UserID: {user.userId}</p>
            <p>UserHash: {user.userHash}</p>
            <img src={"/woodnft-demo/user/" + user.imageUrl} alt={user.name} />
        </div>
    )
}

export default UserInfo;